const scrollers = document.querySelectorAll(".partners__scroll");

//    
if (!window.matchMedia("(prefers-reduced-motion: reduce)").matches) {
  addAnimation();
}

function addAnimation() {
  scrollers.forEach((scroller) => {
    //    
    const scrollerInner = scroller.querySelector(".partners__list");
    if (!scrollerInner) return;

    //  
    scroller.setAttribute("data-animated", true);
    scroller.setAttribute("role", "marquee");

    //  CSS  
    scrollerInner.style.willChange = "transform";

    //        
    const speed = scroller.getAttribute("data-speed") || "60s";
    scrollerInner.style.setProperty("--scroll-speed", speed);

    //     
    const scrollerContent = Array.from(scrollerInner.children);
    if (scrollerContent.length === 0) return;

    scrollerContent.forEach((item) => {
      const duplicatedItem = item.cloneNode(true);
      duplicatedItem.setAttribute("aria-hidden", true);
      scrollerInner.appendChild(duplicatedItem);
    });

    //    
    scroller.addEventListener('mouseenter', () => {
      scrollerInner.style.animationPlayState = 'paused';
    });

    scroller.addEventListener('mouseleave', () => {
      scrollerInner.style.animationPlayState = 'running';
    });

    //      
    scroller.addEventListener('touchstart', () => {
      scrollerInner.style.animationPlayState = 'paused';
    }, {
      passive: true
    });

    scroller.addEventListener('touchend', () => {
      scrollerInner.style.animationPlayState = 'running';
    }, {
      passive: true
    });
  });
}